.page {
  font-size: 15px;
  line-height: 20px;
  font-weight: 400;
  font-family: 'Roboto', Helvetica, sans-serif;
  color: #000000;
  background-color: #FFFFFF; }

.page h1 {
  color: #fffffe;
  font-size: 72px;
  font-weight: 900;
  font-family: 'Raleway', Helvetica, sans-serif;
  letter-spacing: 3.6px;
  text-transform: uppercase;
  margin-left: -4px; }

.page h2 {
  font-family: 'Raleway', Helvetica, sans-serif;
  font-size: 30px;
  line-height: 50px;
  font-weight: 900;
  text-transform: uppercase; }

.page h3 {
  font-family: 'Raleway', Helvetica, sans-serif;
  font-size: 16px;
  line-height: 26px;
  font-weight: 900;
  text-transform: uppercase; }

@media (max-width: 1023px) {
  .page h1 {
    font-size: 38px;
    letter-spacing: 1.9px; }
  .page h2 {
    font-size: 26px;
    letter-spacing: 1.04px; }
  .page h3 {
    font-family: 'Roboto', Helvetica, sans-serif;
    font-size: 14px;
    line-height: 26px;
    font-weight: 400; } }

@media (max-width: 767px) {
  .page h1 {
    font-size: 16px;
    letter-spacing: 0.8px; }
  .page h2 {
    font-size: 20px;
    letter-spacing: 0.8; }
  .page h3 {
    font-size: 16px; } }
